.game {
  width: 400px;
  height: 400px;
  border : 1px solid #aaa;
  position: relative;
}

.me {
  width: 50px;
  height: 10px;
  background-color: teal;
  position: absolute;
  bottom : 0;
  left : 40px;
}

.ball {
  width: 40px;
  height: 40px;
  background-color: blueviolet;
  bottom:  100px;
  position: absolute;
  left : 100px;
  border-radius: 50%;

}